/*Myzone header - Nav*/
.zone_header{
    background-color: #007bec;
    padding: 0 18px;
}


/*Posts Table - Hover Actions => POSTSTABLE COMPONENT*/

.actions{
    visibility:hidden;
}

.actionsParent:hover .actions{
    visibility: visible;
}

/*posts table*/

/*container Component - POSTS TABLE*/
.table{
    overflow: auto;
}

/*postsTable*/
.posts_table{
    margin:32px 80px;
}

/*row with Posts and Create Form*/
.posts_row{
    margin-top: 32px;
    margin-bottom: 32px;
}

/*zone header - individual tab field*/
.tab_name{
    margin:0 16px;
}

/*zone header - logo*/
.zone_header-imgContainer{
    margin: 12px;
}

.recent_item:hover{
    background-color:  #f0faff;
}

/*Dashboard Sidebar*/
.dashboard_recentcol{
    padding:16px 8px;
    border-right:1px solid #e8e8e8;
}

/*Post Page*/
.postsHeader{
    padding:16px 80px;
}

/*Blog Content Container*/
.blogsContent{
    margin:16px 80px;
}

/*dialogue-info*/
.resultinfo{
    position: fixed;
    z-index: 2;
    background-color: white;
    border: 1px solid #1890ff;
    border-radius: 16px;
}

@media (max-width:768px) {
    .posts_table{
        margin-left:20px;
        margin-right:20px;
    }
}

@media (max-width:425px) {
    .tab_name{
        margin:4px;
    }
    .zone_header{
        padding: 0 8px;
    }

    .dashboard_recentcol{
        margin:16px 8px;
        border-right:none;
        border-bottom: 1px solid #e8e8e8;
    }

    .postsHeader{
        padding:8px 20px;
    }

    .blogsContent{
        margin:32px 24px;
    }

    /*change default ant drawer width*/
    .ant-drawer-content-wrapper{
        width:80vw !important
    }
}


/*link active - nav*/
.active::after{
    content: '';
    position: absolute;
    bottom: -19px;
    left: 0;
    right: 0;
    border-bottom: 2px solid white;
}