*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}

/*to move the * into right side of label*/
.ant-form-item-label label{
    direction: rtl;
}

/*remove the password eye icon visibility*/
.removeEye > .ant-input-suffix {
    display: none;
}

.input-label{
    font-weight:500;
}

/*form width - specialized div to make the inner antd element horizontally and vertically center*/
.form-width{
    width: 40%;
}

/*ant drawer footer - style*/
.ant-drawer-footer{
    height:12%;
}


/*RESPONSIVENESS*/
/*form width - responsiveness*/
@media (max-width:985px) {
    .form-width{
        width:60%;
    }
}